			}
		}

		@include grid-media($Small) {
			width: calc(100% - 2rem);
			height: inherit;
			min-height: 100vh;
			padding: 3rem 1rem;
			position: fixed;
			top: 0;
			left: 0;
			overflow: scroll;

			&_inside {
				width: 100%;
				height: inherit;
				min-height: 100vh;
				display: block;
				position: relative;
				left: 0;
				top: 0;
				transform: translate(0, 0);
			}

			&_arrow {
				height: 10vmin;
				//position: relative;
				top: 2vh;

				&_back {
					bottom: 6rem;
					left: 0;
				}

				&_next {
					bottom: 6rem;
					right: 0;
				}
			}
		}
	}

